<template>
    <div>
      <el-row>
        <iframe :src="src" frameborder="0" ></iframe>
        <el-button @click="createPdf">生成报告</el-button>
        <el-button @click="goBack">返回修改</el-button>
      </el-row>
    </div>
</template>

<script>
    export default {
        name: "previewPdf",
        mounted(){
          this.src = this.$route.query['src']
        },
        data(){
          return{
            src:''
          }
        },
      methods:{
          goBack:function () {
            //this.$router.push('/report')
            window.close();
          },
        createPdf:function () {
          //this.$store.commit('changePdfState',true);
          // this.bus.$emit('pdf_created','1');
          let key = localStorage.getItem('pdf');
          if(key==='true'){
            key = 'false';
          }else if(key==='true'){
            key = 'false'
          }else{
            key = 'true'
          }
          localStorage.setItem('pdf', key);
          window.open(this.src,'_self');
        },

      }
    }

</script>

<style scoped>
  div{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    background-color: #282924;

  }

  iframe{
    width: 100%;
    height: 90%;
  }




</style>



